<template>
    <v-container fluid>
        <v-layout justify-space-around fill-height>
            <v-flex xs11>
                <v-card hover>
                    <v-card-title>
                        <h2>Info</h2>
                    </v-card-title>
                    <v-card-text >
                        <v-text-field label="Available Heap (bytes)" readonly=true v-model="ws.heap" />
                        <v-text-field label="Uptime" readonly=true v-model="ws.uptime" />
                        <v-text-field label="SSID" readonly=true v-model="ws.ssid" />
                        <v-text-field label="Signalst√§rke (dBm)" readonly=true v-model="ws.rssi" />
                        <v-text-field label="Aktueller Titel" readonly=true v-model="stream.title" />
                        <v-text-field label="Bitrate (bps)" readonly=true v-model="stream.bitrate" />
                        <!-- <v-text-field label="Music Player" readonly=true :value="$store.state.musicPlayer.getName()" />-->
                        <!-- MOVED to NavigationMenu.vue-->
                        <!-- <div class="text-caption">Media volume</div>
                        <v-slider :min="0" :max="100" :step="5" @change="changeVolume()" :error-messages="errors.volume" v-model="ws.volume" prepend-icon="mdi-volume-high" /> -->                      
                        <!-- <v-switch label="Bluetooth" @change="changeBT()"  :error-messages="errors.bluetooth" v-model="ws.bluetooth" /> -->
                    </v-card-text>
                </v-card>
                <v-card hover>
                    <v-card-title>
                        <h2>Setup</h2>
                    </v-card-title>
                    <v-card-text >
                        <v-switch label="Ansage des Radiosenders vor dem Abspielen" @change="changeAnnounceSettings()" v-model="settings.announcementEnabled" />                                      
                        <v-form @submit.prevent="updateMpdUrl"> 
                        <v-row>
                            <v-col cols="8" md="5" lg="3" sm="10">
                                <v-text-field label="Stream URL des (lokalen) mpd-Servers" v-model="settings.mpdstreamurl"></v-text-field>
                            </v-col>
                            <v-col cols="4" md="3" lg="3" sm="2"><br>
                                <v-btn sstacked type="submit">Update</v-btn>
                            </v-col>
                        </v-row>
                        </v-form>
                    </v-card-text>
                 </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import WebService from '@/services/WebService'

export default {
    
        data: () => ({      
            ws: {       
                heap: null,
                uptime: 'keine Daten',
                ssid: 'nicht definiert',
                rssi: null,
                volume: 20,
                streaming: false,
                bluetooth: false,
                streaminfochanged: false                
            },
            settings: {
                announcementEnabled: false,
                mpdstreamurl: '<nicht konfiguriert>',
            },
            stream: {
                bitrate: '<keine Info>',
                title: '<keine Info>'
            },
            errors: {
                streaming: null,
                bluetooth: null,
                volume: null,                
            },
            timer: ''
        }),

        methods: {
            changeAnnounceSettings() {
                new WebService().postAnnouncementSetting(this.settings.announcementEnabled).then(result => {})
            },

            getSettings(){
                new WebService().getSettings().then(result => {
                    console.log(result);
                    this.settings = result.data                    
                }).catch(error => {
                    console.error(error);
                })
            },

            async updateStreamInfo(){
                new WebService().updateStreamInfo().then(result => {
                    console.log(result);
                    this.stream = result.data;
                }).catch(error => {
                    console.error(error);
                })
            },

            changeBT() {
                this.$store.commit('setMusicPlayer', 0);
                new WebService().postBluetooth(this.ws.bluetooth ).then(result => {
                    console.log(result);
                    this.ws = result.data
                    this.errors.bluetooth = ""
                }).catch(error => {
                    this.ws.bluetooth = !this.ws.bluetooth;
                    console.error(error);
                    this.errors.bluetooth = "Bluetooth Service failed"
                })
            },

            getInfo() {
                new WebService().getInfo().then(result => {
                    console.log(result);
                    this.ws = result.data
                    this.$store.commit('setLoading', false);                                      
                }).catch(error => {
                    this.$store.commit('setLoading', false);
                    console.error(error);
                }).then(() => {
                    if (this.ws.streaminfochanged === "true")
                    {
                        //alert(JSON.stringify(result.data));
                        this.updateStreamInfo();
                    }
                }).catch(error => {
                    console.error(error);
                });
            },

            updateMpdUrl() {
                new WebService().postMpdStreamUrl(this.settings.mpdstreamurl).then(result => {
                    console.log(result);
                }).catch(error => {
                    console.error(error);
                });                
            },

            cancelAutoUpdate () { 
                clearInterval(this.timer)
            }            
        },

        created () {
            this.getSettings();
            this.getInfo();
            this.updateStreamInfo();
            this.timer = setInterval(this.getInfo, 5000)
        },

        beforeDestroy () {
            clearInterval(this.timer)
        }
    }
</script>

